@use './util' as u;

.other-articles {
  width: 100%;
  max-width: 500px;
  margin-inline: auto;

  &__article {
    display: flex;
    gap: 15px;

    &:not(:last-child) {
      margin-bottom: u.rem(34);
    }
  }

  &__image {
    width: 100px;
    height: 130px;
    object-fit: cover;
  }

  &__number {
    margin-bottom: u.rem(10);
    font-size: u.rem(30);
    font-weight: 700;
    color: var(--soft-red);
    line-height: 1;
  }

  &__article-heading {
    margin-bottom: u.rem(6);
  }

  &__link {
    font-size: u.rem(18);
    font-weight: 800;
    color: var(--very-dark-blue);
    text-decoration: none;
    transition: color 100ms linear;

    &:hover,
    &:focus-visible {
      color: var(--soft-red);
    }
  }

  &__description {
    line-height: 1.7;
  }
}

@media screen and (min-width: 1180px) {
  .other-articles {
    display: flex;
    justify-content: space-between;
    max-width: unset;
    margin: 0;

    &__article {
      gap: 25px;

      &:not(:last-child) {
        margin-bottom: 0;
      }
    }

    &__text-section {
      width: 225px;
    }
  }
}
